import * as React from 'react';
import { styled } from '@mui/material/styles';
import { DEFAULT_PROFILE_COVER } from '@/common/constants/default.constant';
import { Typography } from '@mui/material';
import { ProfileContext } from '../..';
import { useFansTotalQuery, useFollowsTotalQuery } from '@/schema';
import { SpecialBoxText } from '@/components/public/special-box';

const PREFIX = 'Basic';

const classes = {
  root: `${PREFIX}-root`,
  cover: `${PREFIX}-cover`,
  coverImg: `${PREFIX}-coverImg`,
  info: `${PREFIX}-info`,
  pirmaryColor: `${PREFIX}-pirmaryColor`,
  secondColor: `${PREFIX}-secondColor`,
};

const Root = styled('div')(({ theme }) => ({
  [`&.${classes.root}`]: {},
  [`& .${classes.cover}`]: {},

  [`& .${classes.coverImg}`]: {
    width: '100%',
    objectFit: 'contain',
  },

  [`& .${classes.info}`]: {
    padding: theme.spacing(3, 4),
  },

  [`& .${classes.pirmaryColor}`]: {
    color: '#fff',
  },

  [`& .${classes.secondColor}`]: {
    color: '#aaa',
  },
}));

export const Basic: React.FC = () => {
  const [userQuery] = React.useContext(ProfileContext);
  const {
    data: fansData,
    loading: fansLoading,
    error: fansError,
  } = useFansTotalQuery({
    variables: {
      owner_uid: userQuery?.user.uid.toString(),
    },
  });
  const {
    data: followsData,
    loading: followsLoading,
    error: followsError,
  } = useFollowsTotalQuery({
    variables: {
      follower_uid: userQuery?.user.uid.toString(),
    },
  });

  return (
    <Root className={classes.root}>
      <div className={classes.cover}>
        <img
          alt="cover"
          className={classes.coverImg}
          src={DEFAULT_PROFILE_COVER}
        />
      </div>
      <div className={classes.info}>
        <div className={classes.pirmaryColor}>
          <Typography gutterBottom variant="h4">
            {userQuery?.user.nickname}
          </Typography>
        </div>
        <Typography
          component="div"
          gutterBottom
          className={classes.pirmaryColor}
        >
          <SpecialBoxText loading={fansLoading} error={!!fansError} />
          {fansData?.fans_total || 0} 粉丝
        </Typography>
        <Typography
          component="div"
          gutterBottom
          className={classes.pirmaryColor}
        >
          <SpecialBoxText loading={followsLoading} error={!!followsError} />
          {followsData?.follows_total || 0} 关注
        </Typography>
        <Typography
          component="div"
          variant="caption"
          className={classes.secondColor}
          gutterBottom
        >
          {userQuery?.user.description}
        </Typography>
        {/* <div>
          <Typography
            variant="subtitle2"
            component="div"
            gutterBottom
            className={classes.pirmaryColor}
          >
            Born
          </Typography>
          <Typography
            className={classes.secondColor}
            component="div"
            variant="caption"
          >
            出生日期
          </Typography>
        </div> */}
      </div>
    </Root>
  );
};

export default Basic;
