import React from 'react';
import { Box, Avatar, Typography, Button } from '@mui/material';
import { MediaPlain } from '@/components/app/media';
import { useFollowHelper } from '@/hooks';
import { ProfileContext } from '../..';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';

export const Top: React.FC = () => {
  const [userQuery, [topShadow]] = React.useContext(ProfileContext);
  const navigate = useNavigate();
  const { following, toggleFollow } = useFollowHelper({
    owner_uid: userQuery?.user.uid as string,
  });

  return (
    <Box sx={(theme) => ({ display: 'flex', paddingBottom: theme.spacing(2) })}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          flex: 1,
          overflow: 'hidden',
        }}
      >
        <Box
          sx={(theme) => ({
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            marginBottom: theme.spacing(6),
          })}
        >
          <Box display="flex" alignItems="center">
            <Avatar src={userQuery?.user.avatar}></Avatar>
            <Box ml={1}>
              <Typography variant="subtitle1">
                {userQuery?.user.nickname}
              </Typography>
              <Typography variant="caption">
                {userQuery?.user.description}
              </Typography>
            </Box>
          </Box>

          {following ? (
            <Button
              sx={(theme) => ({ marginLeft: theme.spacing(1), flexShrink: 0 })}
              onClick={toggleFollow}
            >
              已关注
            </Button>
          ) : (
            <Button
              sx={(theme) => ({ marginLeft: theme.spacing(1), flexShrink: 0 })}
              onClick={toggleFollow}
            >
              关注
            </Button>
          )}
        </Box>
        <Box>
          <Typography gutterBottom variant="subtitle2" component="div">
            {topShadow?.title}
          </Typography>
          <Typography
            gutterBottom
            variant="body2"
            color="textSecondary"
            component="div"
          >
            {topShadow?.description}
          </Typography>
          <div>
            {/* <Typography variant="caption">观看数： TODO</Typography> */}
            {/* <Typography variant="caption">点赞数： TODO</Typography> */}
            <Typography variant="caption">
              发布于：{moment(topShadow?.create_at).format('YYYY-MM-DD')}
            </Typography>
          </div>
        </Box>
      </Box>
      <Box
        sx={(theme) => ({
          flex: 1,
          overflow: 'hidden',
          marginLeft: theme.spacing(2),
        })}
      >
        <MediaPlain
          {...topShadow}
          onClickRoot={() => navigate(`/shadow/${topShadow?.id}`)}
        />
      </Box>
    </Box>
  );
};

export default Top;
